import React, {FC} from 'react'
import Table from '@cloudscape-design/components/table'
import moment from 'moment'
import {LastRuns, Run} from './last-runs'
import {DashboardTableFooter} from './table-footer'

interface Props {
  systemTests: SystemTest[],
}

interface SystemTest {
	name: string,
	invocation: number,
	success: string,
	failure: string,
	duration: string,
	last_runs: Run[],
}


export const DashboardTable: FC<Props> = ({systemTests}) => {
	return <Table
		columnDefinitions={[
			{
				id: 'system-test-name',
				header: 'System name',
				cell: item => item.name,
				sortingField: 'name',
				isRowHeader: true,
			},
			{
				id: 'successes',
				header: 'Successes',
				cell: item => item.success,
			},
			{
				id: 'failures',
				header: 'Failures',
				cell: item => item.failure,
			},
			{
				id: 'duration',
				header: 'Duration',
				cell: item => moment.duration(item.duration, 'seconds').humanize({ss: 1}),
			},
			{
				id: 'last-runs',
				header: 'Last 10 runs (latest on the right)',
				cell: item => <LastRuns lastRuns={item.last_runs}  />
			},
		]}
		items={systemTests
			.sort((a, b) => (a.name > b.name) ? 1 : -1)
			.filter(({invocation}) => invocation > 0)}
		sortingColumn={{sortingField: 'system-test-name'}}
		loadingText='Loading resources'
		variant='embedded'
		contentDensity='compact'
		footer={<DashboardTableFooter systemTests={systemTests.filter(({invocation}) => invocation === 0)} />}
	/>
}
