import React, {useEffect, useState} from 'react'
import Header from '@cloudscape-design/components/header'
import SpaceBetween from '@cloudscape-design/components/space-between'
import axios from 'axios'
import Spinner from '@cloudscape-design/components/spinner'
import Box from '@cloudscape-design/components/box'
import {useParams} from 'react-router'
import {BASE_API} from '../../constants'
import {DashboardTable} from './table'
import moment from 'moment/moment'

export const Dashboard = () => {
	const [loading, setLoading] = useState(true)
	const [systemTests, setSystemTests] = useState([])
	const [lastUpdated, setLastUpdated] = useState('')
	const { version } = useParams()

	useEffect(() => {
		document.title = 'AWS system tests - Last executions summary'

		const apiUrl = version
			? BASE_API + `/open-source/system-tests/version/${version}.json`
			: BASE_API + '/open-source/system-tests/dashboard.json'
		axios.get(apiUrl).then((response) => {
			setLastUpdated(moment(response.headers['last-modified']).calendar())
			setSystemTests(response.data)
			setLoading(false)
		})
	}, [])

	let source
	if (version) {
		const [airflowVersion, amppVersion] = version.split('_')
		source = `Airflow version ${airflowVersion} and Amazon provider package version ${amppVersion}`
	} else {
		source = <>the latest <a href='https://github.com/apache/airflow'>Apache Airflow codebase</a></>
	}

	return <SpaceBetween size='m'>
		<Header variant='h1'>Apache Airflow - Amazon Provider Package Health</Header>
		<Header variant='h2'>View the health of AWS service integrations for Apache Airflow</Header>

		<p>
			This live dashboard displays the current health of AWS service integrations available in
			the <a href='https://airflow.apache.org/docs/apache-airflow-providers-amazon/stable/index.html'>Amazon
				Provider package</a> of Apache Airflow.<br />
			The following table shows data for all runs from the past 7 days of
			the <a href='https://github.com/apache/airflow/blob/main/tests/system/providers/amazon/README.md'>AWS System
				Tests</a> using {source}.
		</p>

		{loading
			? <Box textAlign='center'><Spinner size='large' /></Box>
			: <DashboardTable systemTests={systemTests} />
		}

		<SpaceBetween size='s'>
			<p>{lastUpdated}</p>
			<p className='italic'>
				Some system tests fall outside the scope of the health dashboard, for several potential reasons:
				(1) infrastructure setup may still be in progress, (2) the test may have been removed due to security
				concerns, or (3) it may require integration with a non-AWS service.
			</p>
			<p className='italic'>
				The data presented here resulted from the test procedures, inputs and parameters as described&nbsp;
				<a href='https://github.com/apache/airflow/blob/main/tests/system/providers/amazon/README.md'>here</a>
				&nbsp;and <a href='https://aws-mwaa.github.io/open-source/system-tests/description.html'>here</a>.
				This data is provided as general information only. We do not guarantee the accuracy of this data.
				Your experience and performance may vary.
			</p>
		</SpaceBetween>
	</SpaceBetween>
}
