import React, {FC} from 'react'
import ExpandableSection from '@cloudscape-design/components/expandable-section'

interface SystemTest {
    name: string;
}

interface Props {
    systemTests: SystemTest[];
}

export const DashboardTableFooter: FC<Props> = ({systemTests}) => {
	if (systemTests.length == 0)
		return null

	return <ExpandableSection
		headerText='The following tests are not run regularly for a variety of reasons. See footnotes for more details.'
	>
		<ul>
			{systemTests
				.sort((a, b) => (a.name > b.name) ? 1 : -1)
				.map((systemTest, index) => <li key={index}>{systemTest.name}</li>)}
		</ul>
	</ExpandableSection>
}
