import React, {FC} from 'react'
import StatusIndicator from '@cloudscape-design/components/status-indicator'
import Popover from '@cloudscape-design/components/popover'
import moment from 'moment'

export interface Run {
  value: number,
  timestamp: number,
  type: string
}

interface Props {
  lastRuns: Run[],
}

export const LastRuns: FC<Props> = ({lastRuns}) => {
	return <>
		{lastRuns.map((lastRun, index) =>
			<Popover
				key={index}
				dismissButton={false}
				position='top'
				size='small'
				triggerType='custom'
				content={<span>{moment.unix(lastRun['timestamp']).calendar()}</span>}
			>
				<StatusIndicator key={index} type={lastRun['type'] === 'success' ? 'success' : 'error'} />
			</Popover>
		)}
	</>
}
