import React from 'react'
import {HashRouter, Route, Routes} from 'react-router-dom'
import '@cloudscape-design/global-styles/index.css'
import './App.css'
import {NotFound} from './errors/404'
import {Dashboard} from './open-source/system-tests/dashboard'

const App = () => {
	return <HashRouter>
		<Routes>
			<Route path='/open-source/system-tests/dashboard.html' element={<Dashboard />} />
			<Route path='/open-source/system-tests/version/:version.html' element={<Dashboard />} />
			<Route path='*' element={<NotFound />} />
		</Routes>
	</HashRouter>
}

export default App
